.owl-carousel {
    &.owl-padding {
        .owl-stage-outer {
            padding-top: 1.8rem;
            padding-bottom: 1.8rem;
        }
    }
    &.owl-padding-sm {
        .owl-stage-outer {
            padding-top: 0.5rem;
            padding-bottom: 0.5rem;
        }
    }

    .owl-dots {
        .owl-dot, .owl-dot:hover, .owl-dot {
            span {
                background: $brand-primary;
                width: 12px;
                height: 12px;
                border: 4px solid transparent;
                margin: 8px 4px;
            }

            &.active {
                span {
                    transition: all 0.3s;
                    background: none;
                    border: 4px solid $brand-primary;
                }
            }
        }
    }

    &.owl-dots-left {
        .owl-dots {
            text-align: right !important;
        }
    }

    &.owl-equalize-height {
        .owl-stage {
            display: flex;
            align-items: stretch;
        }
    }
}



.lb-data .lb-close {
    width: 1.7rem;
    height: 1.7rem;
    background-size: 80%;
    background-position: center center;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.lb-outerContainer {
    border-radius: 0;
}

.lb-dataContainer {
    .lb-caption {
        font-family: $headings-font-family;
        font-weight: $headings-font-weight;
        font-size: 1rem;
    }
    .lb-details {
        padding: 0.5rem 0;
    }
}


.datepicker td, .datepicker th {
    text-align: center;
    width: 14.285%;
    height: 40px;
    font-size: 0.8rem;
    font-weight: $font-weight-normal;
    border-radius: 0;
    transition: all 0.3s;

    &:hover {
        background: darken($dark, 1%) !important;
    }

    &.today {
        &:hover {
            background: $brand-primary !important;
        }
    }
}

.datepicker {
    tr:last-of-type {
        th {
            border-bottom: 1px solid $dark;
            background: $dark;
        }
    }
    tr {
        border-bottom: 1px solid lighten($body-bg, 4%);

        &:last-of-type {
            border-bottom: none;
        }

        td {
            border-right: 1px solid lighten($body-bg, 4%);

            &:last-of-type {
                border-right: none;
            }
        }
    }
}



.datepicker thead td, .datepicker thead th {
    font-family: $headings-font-family;
}

.datepicker.dropdown-menu {
    width: calc(100% - 2rem);
    padding: 0.5rem 1rem;
    border-radius: 0;
    background: $body-bg !important;
    border: 1px solid $text-muted;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
    .table-condensed {
        width: 100%;
    }
}

.datepicker table tr td.active.active {
    background: $brand-primary !important;
    color: $dark !important;

    &:hover {
        background: darken($brand-primary, 5%) !important;
        color: $dark !important;
    }
}

.datepicker {
    &::before, &::after {
        display: none !important;
    }
    .month, .year {
        border-radius: 0;
        transition: all 0.3s;

        &.focused.active {
            background: $brand-primary !important;
            border-radius: 0;

            &:hover {
                background: darken($brand-primary, 5%) !important;
            }
        }
        &.focused, &.active {
            background: $brand-primary !important;
            border-radius: 0;

            &:hover {
                background: darken($brand-primary, 5%) !important;
            }
        }

        &:hover {
            background: darken($brand-primary, 5%) !important;
        }
    }

    .clear, .today {
        background: $brand-primary;
        color: #fff;
        text-transform: uppercase;
        letter-spacing: 0.08em;
        font-weight: $font-weight-bold;
        height: 30px;

        &:hover {
            background: darken($brand-primary, 5%) !important;
        }
    }
}



.timeselector {
    z-index: 9999 !important;
    background: $body-bg;
    border-radius: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    border-color: $text-muted !important;
    padding: 0;
}

.timeselector-item {
    width: 50%;
}

.timeselector-hour .timeselector-button {
    border-right: 1px solid $text-muted;
    &:hover {
        border-right: 1px solid $text-muted;
    }
}

.timeselector-minute .timeselector-button {
    border-left: 1px solid $text-muted;
    &:hover {
        border-left: 1px solid $text-muted;
    }
}

.timeselector-button {
    width: 3rem;
    height: 2rem;
    line-height: 1.5rem;
    border-radius: 0;
    border: none;
    margin: 0;
    background: none;
    padding: 5px 45px;
    transition: all 0.3s;
    background: $body-bg;
    color: $light !important;
    width: 100%;

    &:hover {
        border-color: $brand-primary !important;
        background: $brand-primary !important;
        color: #fff !important;
    }
}


.timeselector-value {
    background: lighten($body-bg, 5%);
    padding: 10px;
    color: $light;
    border: none;
}

.timeselector-separator {
    margin-top: 0;
    padding: 10px 1px;
    width: 4px;
    background: lighten($body-bg, 5%);
    position: absolute;
    top: 50%;
    left: 50%;
    color: $light;
    transform: translate(-50%, -50%);
}

.bootstrap-select {
    display: block !important;
    width: 100% !important;
    max-width: 100% !important;
    .bs-select-form-control.dropdown-toggle {
        display: block !important;
        border: 1px solid $input-border-color !important;
        padding: $btn-padding-y-lg $btn-padding-x-lg !important;
        outline: none !important;
        box-shadow: none;

        &::after {
            margin-left: -0.5rem !important;
        }
    }

    .dropdown-menu {
        padding: 0;
        border-radius: 0;
        border-color: darken($dark, 1%);
        background: $dark !important;
        // border-bottom: none;
        box-shadow: 0 0 7px rgba(0, 0, 0, 0.3);
        max-height: 200px !important;
        // transform: translate3d(0px, -252px, 0px);
        max-width: 100%;

        .dropdown-item {
            padding: 0.7rem 1rem;
            transition: all 0.3s;
            outline: none !important;
            font-size: 0.9rem;
            transform: none !important;
            font-weight: 300;
            color: $light !important;
            border-bottom: 1px solid $body-bg;

            &.active, &:active {
                background: $body-bg !important;
                color: $light !important;
            }
        }
    }

    .filter-option-inner-inner {
        color: $input-placeholder-color;
        font-size: 0.85rem;
        font-weight: 300;
        font-family: $font-family-sans-serif;
        line-height: 1.5rem !important;

        &.selected {
            font-size: 1rem;
            color: $light;
            font-weight: $font-weight-normal;
        }
    }

    .bs-searchbox {
        padding: 0.5rem;
        input {
            padding-left: 1rem;
            padding-right: 1rem;
            background: darken($dark, 1%);
            border: none !important;
            &:focus {
                box-shadow: none;
                border: 1px solid #ddd;
            }

            &::placeholder {
                font-weight: 300;
            }
        }
    }

    .no-results {
        margin: 0 0.5rem 0.5rem 0.5rem;
        padding: 0.2rem 1rem;
        background: darken($dark, 1%);
        font-size: 0.85rem !important;
        font-weight: 300 !important;
    }


    button.dropdown-toggle {
        &::after {
            border: none;
            content: '\f078' !important;
            font-family: 'Font Awesome\ 5 Free';
            font-weight: 900;
            font-size: 0.65rem;
            vertical-align: middle !important;
            padding-left: 0.1rem;
            transform: translateY(4px);
            color: $light;
        }
    }
    .dropdown-toggle[aria-expanded="true"] {
        &::after {
            content: '\f077' !important;
        }
    }
}
