.about-services-block {
    &:nth-of-type(odd) {
        background-position: center center !important;
        background-size: cover !important;
        min-height: 200px;

        @include media-breakpoint-up(lg) {
            min-height: 400px;
        }
    }
    &:nth-of-type(even) {
        text-align: center;
        background: $light;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
