/* ===============================================================
     [FORMS] EDIT BOOTSTRAP DEFAULT STYLING
  =============================================================== */
.booking-form-holder {
    border-top: 3px solid $brand-primary;
    position: relative;

    &::before {
        content: '\f272';
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: $brand-primary;
        color: #fff;
        text-align: center;
        line-height: 50px;
        display: block;
        position: absolute;
        top: -25px;
        left: 3rem;
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
    }
}


.invalid-feedback {
    padding: 0.15rem 1rem;
    color: #fff !important;
    background: $danger;
}
.valid-feedback {
    padding: 0.15rem 1rem;
    color: #fff !important;
    background: $success;
}

.form-control.is-invalid,
.was-validated .form-control:invalid {
    background-size: calc(.5rem + .25rem) calc(.5rem + .25rem);

    &:focus {
        box-shadow: none !important;
    }
}

.form-control.is-valid,
.was-validated .form-control:valid {
    background-size: calc(.5rem + .25rem) calc(.5rem + .25rem);

    &:focus {
        box-shadow: none !important;
    }
}

.form-group.is-invalid {
  ~ .invalid-feedback {
    display: block;
  }
}

label:not(.input-tabs-label):not(.custom-control-label) {
    font-size: 0.85rem;
    text-transform: uppercase;
    padding: 0.5rem 0;
    margin: 0 !important;
}

.input-tabs-label {
    border: 1px solid $input-border-color;
    margin: 0;
    height: $input-height;
    width: 100%;
    cursor: pointer;
    display: flex;
    align-items: center;
    color: $input-placeholder-color;
    font-size: 0.85rem !important;
    font-weight: 300;
    transition: all 0.3s;

    &:hover {
        background: $light;
    }
}

.input-tabs {
    display: flex;
    align-items: center;
    input {
        display: none;
        &:checked + label.input-tabs-label {
            background: $success;
            color: #fff;
            border-color: $success;
            font-weight: $font-weight-bold;
        }
    }
}


.label-optional {
    position: relative;
    &::before {
        content: '- Optional';
        font-size: 0.75rem;
        color: #aaa;
        font-weight: $font-weight-normal;
        text-transform: capitalize;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -4.3rem;
        display: block;
    }
}


.alert {
    border-radius: 0;
    &-light {
        background: $light;
    }
}

.alert-dismissible .close {
    font-size: 0.85rem;
    transition: all 0.3s;
    padding-top: 1.1rem;
    padding-bottom: 1.1rem;
    opacity: 1;
    color: $light !important;

    &:focus {
        outline: none !important
    }
}

.custom-checkbox .custom-control-label::before {
    border-radius: 0 !important;
    background: darken($light, 5%) !important;
    border: none !important;
    width: 1.2rem;
    height: 1.2rem;
}
.custom-checkbox .custom-control-input:checked ~ .custom-control-label::after {
    border-radius: 0 !important;
    background-color: $brand-primary !important;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E");
    border: none !important;
    width: 1.2rem;
    height: 1.2rem;
}

.custom-control-label {
    line-height: 1.7rem;
    font-size: 0.85rem;
    font-weight: 300;
    padding-left: 0.5rem;
    cursor: pointer;
    color: $light !important;
}
