/* ===============================================================
     SERVICES STYLING
  =============================================================== */
.services-item {
    transition: all 0.3s;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        display: block;
        height: 4px;
        width: 100%;
        background: $brand-primary;
        transition: all 0.3s;
    }
    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: url(../img/pattern.png);
        background-size: 12rem;
        opacity: 0;
        display: block;
        transition: all 0.3s;
    }

    &:hover {
        background: $body-bg;
        box-shadow: 0 2px 7px rgba(0, 0, 0, 0.3);
        transform: translateY(4px);

        &::after {
            opacity: 0.04;
        }

        &::before {
            opacity: 1;
        }
    }
}



/* ===============================================================
     FEATURED DISHES STYLING
  =============================================================== */
.featured-dishes-item {
    position: relative;
    &::before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08);
        background: $dark;
        border: 3px solid transparent;
        z-index: -1;
        transition: all 0.3s;
    }
    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background: url(../img/pattern.png);
        background-size: 12rem;
        opacity: 0;
        display: block;
        transition: all 0.3s;
        z-index: -1;
    }

    &.discounted {
        .discounted-ribbon {
            padding: 0 1rem;
            height: 2rem;
            line-height: 2rem;
            color: #fff;
            background: $brand-primary;
            position: absolute;
            top: -1rem;
            right: 0;
            font-weight: $font-weight-bold;
            text-transform: uppercase;
            font-size: 0.8rem;
            transition: all 0.2s;
        }
    }

}

.featured-dishes-slider {
    .owl-item.center {
        .featured-dishes-item {
            .discounted-ribbon {
                top: -1.9rem !important
            }
            &::before {
                transform: scaleY(1.15);
                border: 3px solid $brand-primary;
            }
            &::after {
                opacity: 0.04;
                transform: scaleY(1.15);
            }

        }
    }
}



/* ===============================================================
     HOME MENU STYLING
  =============================================================== */
.menu-home-item-image, .menu-item-image {
    position: absolute;
    height: 120%;
    top: -10%;
    right: 0;

    img {
        height: 100%;
    }

    @include media-breakpoint-down(sm) {
        height: 100%;
        top: 0;
    }
}

.menu-item {
    padding-right: 35% !important;
    position: relative;
    border-radius: 0 30rem 30rem 0;
    border-left: 3px solid $brand-primary;
}

.menu-item {
    .price {
        height: 1.5rem;
        line-height: 1.5rem;
        padding: 0 1rem;
        background: $brand-primary;
        position: absolute;
        top: -1rem;
        left: 2rem;
        font-size: 0.8rem;
        color: #fff;

        &::before {
            content: '';
            border-bottom: 1rem solid darken($brand-primary, 20%);
            border-left: 0.5rem solid transparent;
            display: block;
            position: absolute;
            left: -.5rem;
            top: 0;
        }
    }
}

.signature-dishes-slider {
    img {
        max-width: 10rem;
    }
}

.menu-item-minimal {
    .menu-item-separator {
        height: 1px;
        border-bottom: 1px dashed #ccc;
        margin: 0 1rem;
        width: 100%;
    }
}


/* ===============================================================
     SINGLE DISH STYLING
  =============================================================== */
.dish-single-price {
    padding: 0.3rem 1rem;
    color: #fff !important;
    background: $brand-primary;
    display: inline-block;
    position: relative;

    &::after {
        content: '';
        display: block;
        border-top: 1rem solid darken($brand-primary, 10%);
        border-right: 0 solid transparent;
        border-left: 0.8rem solid transparent;
        position: absolute;
        bottom: -1rem;
        left: 0;
    }
}

.dish-detail-img {
    background-size: cover !important;
}



/* ===============================================================
     CHEFS STYLING
  =============================================================== */
.chefs-item {
    transition: all 0.3s;
    border: 2px solid transparent;

    p {
        transition: all 0.3s;
    }

    &:hover {
        background: $brand-primary;
        color: #fff;
        border-color: $brand-primary;
        box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);

        p {
            color: #fff !important;
            transition: all 0.3s;
        }

        .social-link {
            color: $brand-primary;
            background: #fff;
        }
    }
}


.social-link {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: $light;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
    font-size: 0.8rem;
    transition: all 0.3s;

    &:hover, &:focus {
        text-decoration: none;
    }
}


/* ===============================================================
     GALLERY STYLING
  =============================================================== */
.gallery-item {
    transition: all 0.2s;
    padding-bottom: 70%;
    display: block;
    background-size: cover !important;
    background-position: center center !important;
    position: relative;

    &-overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        padding: 2rem;
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;
        background: rgba($brand-primary, 0.8);
        opacity: 0;
        transition: all 0.3s;

        &-title {
            color: #fff;
            opacity: 0;
            transform: translateX(0.6rem);
            transition: all 0.3s;
            padding-left: 1rem;
            border-left: 3px solid #fff;
        }
    }

    &:hover {
        .gallery-item-overlay {
            opacity: 1;
            &-title {
                transform: none;
                opacity: 1;
            }
        }
    }
}


/* ===============================================================
     CONTACT STYLIG
  =============================================================== */
.branch-img-holder {
    padding-bottom: 70%;
    position: relative;
}

.branch-info {
    position: absolute;
    left: 2rem;
    max-width: calc(100% - 2rem);
    background: $brand-primary;
    top: -2rem;
    &::after {
        content: '';
        display: block;
        border-bottom: 2rem solid darken($brand-primary, 20%);
        border-right: 0 solid transparent;
        border-left: 1rem solid transparent;
        position: absolute;
        top: 0;
        left: -1rem;
    }
}

.contact-map {
    @include media-breakpoint-up(lg) {
        min-height: 350px;
    }
    min-height: 200px;
}
