/* ===============================================================
     BOOKING STYLING
  =============================================================== */
.booking-receipt {
    position: relative;

    &::before, &::after {
        content: '';
        display: block;
        width: 100%;
        height: 1rem;
        background: url(../img/separator-bottom.svg) no-repeat;
        background-position: top center;
        background-size: cover;
        position: absolute;
        bottom: -1rem;
        left: 0;
    }

    &::before {
        transform: rotate(180deg);
        bottom: auto;
        top: -1rem;
    }
}

.booking-form-holder {
    border: 1px solid lighten($body-bg, 5%);
}
