/*

==========================================
STYLE SWITCHER FOR DEMO
==========================================

*/

#style-switch-button {
    position: absolute;
    top: 0;
    left: -3rem;
    border-radius: 0;
    z-index: 2;
    font-size: 1.2rem;
    color: $brand-primary;
    border: none;
    box-shadow: -5px 0 5px rgba(0, 0, 0, 0.4);
    background: $body-bg;
    border-radius: 30rem 0 0 30rem;
    z-index: 9999;
    width: 3rem;
    height: 2.5rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    transition: all 0.3s;

    @include media-breakpoint-down(md) {
        top: calc(50% - 4rem);
        transform: translateY(-50%);
    }

}

#style-switch {
    width: 300px;
    padding: 30px;
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    right: -300px;
    background: $dark;
    z-index: 9999;
    transition: all 0.6s ease-in-out;

    &.active {
        right: 0;
        box-shadow: 0 0 7px rgba(0, 0, 0, 0.4);

        #style-switch-button {
            background: $dark;
        }
    }
}

.switcher-overlay {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.85);
    z-index: 9998;
    display: none;
}



.toggle-switcher {
    display: inline-block;
    padding: 0.3rem;
    border: 1px solid $text-muted;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
    input {
        display: none;

        &:checked {
            & + .toggle-switcher-label {
                .span-right {
                    color: #fff;
                }
                .span-left {
                    color: inherit;
                }
                &::before {
                    transform: translateX(100%);
                    opacity: 1;
                }
            }
        }
    }
    .toggle-switcher-label {
        display: flex;
        align-items: center;
        position: relative;
        padding: 0 !important;
        margin: 0 !important;
        cursor: pointer;
        color: $light;
        width: 12rem;

        .span {
            width: 50%;
            font-size: 0.7rem;
            height: 2rem;
            line-height: 2.1rem;
            text-align: center;
            transition: all 0.3s cubic-bezier(1.000, 0.010, 0.005, 1.010);
            color: inherit;

            &-left {
                color: #fff;
            }
        }

        &::before {
            content: '';
            position: absolute;
            width: 50%;
            height: 2rem;
            top: 0;
            left: 0;
            background: $brand-primary;
            transition: all 0.3s cubic-bezier(1.000, 0.010, 0.005, 1.010);
            z-index: -1;
        }
    }
}

.theme-color-pallete {
    padding: 0.3rem;
    border: 1px solid $text-muted;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);

    input {
        display: none;
    }
}

.theme-color-label {
    width: 100%;
    height: 2rem;
    line-height: 2rem;
    padding: 0 !important;
    font-size: 0.65rem !important;
    cursor: pointer;
    margin: 0;
    text-align: center;
    color: #fff;

    &.red {
        background: #ff3f3f;
    }
    &.pink {
        background: #f54291;
    }
    &.blue {
        background: #2b90d9;
    }
    &.green {
        background: #41d088;
    }
    &.sea {
        background: #51acad;
    }
    &.violet {
        background: #ab7be0;
    }
    &.yellow {
        background: #FD8A06;
    }
}
