/* ===============================================================
ADMIN PAGES STYLING
=============================================================== */
.admin-nav {
    &::before {
        display: none !important;
    }

    .nav-link {
        font-size: 0.8rem;
        font-weight: 400;
        background: $dark;
        margin-bottom: 0.25rem;
        padding: 0.7rem 2rem;

        &:last-of-type {
            margin-bottom: 0;
        }

        &:not(.active):hover {
            background: darken($dark, 3%);
            color: $light !important;
        }

        &.active {
            background: darken($dark, 3%) !important;
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
            border-left: 4px solid $brand-primary;
            color: $light !important;
            margin:  0.3rem  0 0.6rem 0;

            i {
                color: $brand-primary;
            }
        }
    }

}

.admin-info-title {
    min-width: 12rem;
}

.admin-booking-item {
    position: relative;
    perspective: 500px;
    margin-bottom: 4rem;

    &:last-of-type {
        margin-bottom: 2rem;
    }

    &:hover {
        .admin-booking-cta {
            opacity: 1;
            transform: none;
        }
    }
}

.admin-booking-cta {
    width: 100%;
    position: absolute;
    left: 0;
    top: 100%;
    transition: all 0.3s;
    transform-origin: top left;
    transform: rotateX(-90deg);
    z-index: -1;
    opacity: 0;

    @include media-breakpoint-down(sm) {
        transform: none;
        opacity: 1;
    }
}

.admin-booking-special-request {
    background: darken($dark, 1%);
}
