/* ===============================================================
     SCROLL TOP BUTTON
  =============================================================== */
#scrollTop {
    background: $brand-primary;
    color: #fff;
    cursor: pointer;
    width: 100px;
    height: 40px;
    line-height: 40px;
    padding: 0 10px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    position: fixed;
    top: calc(50% - 20px);
    right: -110px;
    z-index: 9998;
    font-size: 0.8em;
    transition: all 0.3s;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

#scrollTop.active {
    right: -64px;
}

#scrollTop:hover {
    right: -10px;
}

#scrollTop i {
    margin: 0 10px;
}
